import React, {Component} from 'react';
import Header from '../components/Header';
import DollarRate from './Rate/DollarRate';
import EuroRate from './Rate/EuroRate';
import Bitcoin from './Crypto/Bitcoin';
import Ethereum from './Crypto/Ethereum';
import XRP from './Crypto/XRP';
import AddCity from './Weather/AddCity/AddCity';
import ShowWeather from './Weather/ShowWeather/ShowWeather';
import ToggleWeather from './Weather/ToggleWeather/ToggleWeather';
import Settings from './Settings/Settings';

class Wrapper extends Component {
    render() {
        return (
            <React.Fragment>
                <Header/>
                    <div id="main-content">
                        <section id = "rate">
                            <div className="container">
                                <h2>Курсы валют</h2>
                                <div className="row">
                                    <DollarRate/>
                                    <EuroRate/>
                                    <DollarRate/>
                                    <EuroRate/>
                                    <DollarRate/>
                                    <EuroRate/>
                                </div>
                            </div>
                        </section>
                        
                        <section id = "crypto">
                            <div className="container">
                                <h2>Курс крипты</h2>
                                <div className="row">
                                    <Bitcoin/>
                                    <Ethereum/>
                                    <XRP/>
                                </div>
                            </div>
                        </section>

                        <section id = "weather">
                            <div className="container">
                                <h2>Прогноз погоды</h2>
                                <div className="row">
                                    <AddCity/>
                                </div>
                                <div className="row">
                                    <ShowWeather/>
                                </div>
                                <div className="row">
                                    <ToggleWeather/>
                                </div>
                            </div>
                        </section>
                    </div>
                <Settings/>
            </React.Fragment>
        ) 
    }
}

export default Wrapper;