import React, {Component} from 'react';
import {connect} from 'react-redux';

import {dispatchItem} from '../../actions/dispatchItem';

import EuroRateComponent from '../../components/Rate/EuroRate/EuroRate';

class EuroRate extends Component {

    componentDidMount() {
        this.props.onChangeValue(1);
    }

    render() {
        return (
            (this.props.localState.status) ?
            (<React.Fragment>
                <EuroRateComponent value = {this.props.localState.value}/>
             </React.Fragment>)
             :
             (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[1]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EuroRate);