
import React, {Component} from 'react';
import {connect} from 'react-redux';
import {addCity} from '../../../actions/index';
import {dispatchWeather} from '../../../actions/dispatchWeather';
import AddCityComponent from '../../../components/Weather/AddCity/AddCity';

class AddCity extends Component {

    addCity = () => {
        let input = document.getElementById('addCity__input');
        let re = /^[a-zA-Z]+$/;
        if (!re.test(input.value)) {
            input.value = "Please write only english letters";
            return;
        }
        let id = Date.now().toString();
        this.props.onAddCity(id, input.value);
        this.props.onSetWeather(id, input.value);
        input.value = "";
    }

    componentDidMount() {
        const input = document.getElementById('addCity__input');
        input.addEventListener('click', function(event){
            input.value = "";
            event.preventDefault();
        });
        const button = document.getElementById("addCity__button")
        button.addEventListener('click', (event) => {
            this.addCity();
            event.preventDefault()
        })
    } 

    render() {
        return(
            <React.Fragment>
                <AddCityComponent/>
            </React.Fragment>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        localState: state
    }
}

const mapDispatchToProps = (dispatch) => {
    return{
        onAddCity: (id, city) => {
            dispatch(addCity(id, city))
        },
        onSetWeather: (id, city) => {
            dispatch(dispatchWeather(id, city))
        }   
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(AddCity);