
export const addItem = (id, idCategory, name, status) => (
    {
        type: 'ADD_ITEM',
        id,
        idCategory,
        name,
        status
    }
)

export const addTopic = (id, name, status) => (
    {
        type: 'ADD_TOPIC',
        id,
        name,
        status
    }
)

export const toggleTopic = (id) => (
    {
        type: 'TOGGLE_TOPIC',
        id
    }
)

export const changeTopic = (id, value) => (
    {
        type: 'CHANGE_TOPIC',
        id,
        value
    }
)

export const toggleItem = (id) => (
    {
        type: 'TOGGLE_ITEM',
        id
    }
)

export const changeItem = (id, value) => (
    {
        type: "CHANGE_ITEM",
        id,
        value
    }
)


export const addCity = (id, city) => (
    {
        type: "ADD_CITY",
        id,
        idCategory: 3,
        city,
        temprature: ""
    }
)

export const removeCity = () => (
    {
        type: "REMOVE_CITY"
    }
)


export const toggleShouldDelete = (id) => (
    {
        type: "TOGGLE_DELETE",
        id
    }
)



export const setWeather = (id, value) => (
    {
        type: "SET_WEATHER",
        id,
        value: Math.round(value * 10) / 10 + " °C"
    }
)