import {changeItem} from './index';
import {getData} from '../functions/getData';


export const dispatchItem = (id) => {

    return (dispatch) => {

        dispatch({type: "START_DISPATCH"});

        let fetch = getData(id);
        
        fetch()
            .then(data => {
                if(data)
                     dispatch(changeItem(id, data));
                else 
                     dispatch({type: "ERROR"});
            });
    }
}