import {
    DOLLAR_RATE_ID,
    EURO_RATE_ID,
    GBP_RATE_ID,
    CHF_RATE_ID,
    JPY_RATE_ID,
    AUD_RATE_ID,
    BITCOIN_ID,
    ETHEREUM_ID,
    XRP_ID,
    LTC_ID,
    STELLAR_ID,
    BTCCSH_ID,
    EOS_ID,
    TETHER_ID,
    TRON_ID,
    MONERO_ID
} 
from '../actions/const';

import {
    fetchDollarRate,
    fetchEuroRate,
    fetchGBPRate,
    fetchCHFRate,
    fetchJPYRate,
    fetchAUDRate,
    fetchBitcoin,
    fetchEthereum,
    fetchXRP,
    fetchLTC,
    fetchSTELLAR,
    fetchBTCCSH,
    fetchEOS,
    fetchTETHER,
    fetchTRON,
    fetchMONERO
} 
from './index';

export const getData = (id) => {
    switch(id) {
        case DOLLAR_RATE_ID :
            return fetchDollarRate
        case EURO_RATE_ID :
            return fetchEuroRate
        case GBP_RATE_ID :
            return fetchGBPRate
        case CHF_RATE_ID :
            return fetchCHFRate
        case JPY_RATE_ID :
            return fetchJPYRate
        case AUD_RATE_ID :
            return fetchAUDRate
        case BITCOIN_ID : 
            return fetchBitcoin
        case ETHEREUM_ID :
            return fetchEthereum
        case XRP_ID :
            return fetchXRP
        case LTC_ID :
            return fetchLTC
        case STELLAR_ID :
            return fetchSTELLAR
        case BTCCSH_ID :
            return fetchBTCCSH
        case EOS_ID :
            return fetchEOS
        case TETHER_ID :
            return fetchTETHER
        case TRON_ID :
            return fetchTRON
        case MONERO_ID :
            return fetchMONERO
    }
}
