import React, {Component} from 'react';
import {connect} from 'react-redux';
import {toggleShouldDelete} from '../../../actions/index';

class ToggleWeather extends Component {

    toggleWeather = () => {
        const ul = document.getElementById("showWeather__ul");
        ul.addEventListener('click', (event) => {
            let target = event.target;
            if(target.tagName !== "LI")
                return;
            target = target.getAttribute("data-key");
            this.props.onToggleWeather(target);
        })
    }

    componentDidMount() {
        this.toggleWeather()
    }

    render() {
        return(
            <React.Fragment>
                <button onClick = {this.props.onRemove}>
                    Save changes
                </button>
            </React.Fragment>
        )
    }
}

// const mapStateToProps = (state) => {
//     return {
//         localState: state.weather
//     }
// }

const mapDispatchToProps = (dispatch) => {
    return {
        onToggleWeather: (id) => {
            dispatch(toggleShouldDelete(id))
        },
        onRemove: () => {
            dispatch({type:"REMOVE_CITY"})
        }
    }
}
export default connect(null, mapDispatchToProps)(ToggleWeather);