import {setWeather} from './index';
import {WEATHER_ID} from './const';
import {getData} from '../functions/getData';


export const dispatchWeather = (id, city) => {

    return (dispatch) => {

        dispatch({type: "START_DISPATCH_WHEATHER"});

        
        // let fetch = getData(WEATHER_ID, city);
        const fetchWeather = (city) => {
            const url = 'https://api.openweathermap.org/data/2.5/find?q=';
            const appID = '8c6b34946a9936a4f503e2797bfc6840';
            const query = `&type=like&APPID=${appID}`;
            fetch(`${url}${city}${query}`)
                .then(response => response.json())
                .then(data => dispatch(setWeather(id, data.list[0].main.temp - 273.15)))
                .catch(message => console.log(message));
        }

        fetchWeather(city);
        
        // fetch()
        //     .then(data => {
        //         if(data)
        //              dispatch(setWeather(id, data));
        //         else 
        //              dispatch({type: "ERROR_DISPATCH_WHEATHER"});
        //     });
    }
}