
let initialState = [];

if(localStorage.getItem("store")) {
    const store = JSON.parse(localStorage.getItem("store"));
    if(store.weather)
        initialState = store.weather
}

const weather = (state = initialState, action) => {

    if (action.type === "ADD_CITY") {
        let {id, idCategory, city, temprature} = action;
        return [
            ...state, 
            {
                id,
                idCategory,
                city,
                temprature,
                shouldDelete: false
            }
        ]
    }

    if(action.type === "SET_WEATHER") {
        return state.map(i =>
            (i.id == action.id) ?
            {...i, temprature: action.value}:
            i);
    }

    if (action.type === "TOGGLE_DELETE") {
        return state.map(i =>
            (i.id == action.id) ?
            {...i, shouldDelete: !i.shouldDelete}:
            i);
    }

    if (action.type === "REMOVE_CITY") {
        return state.filter(function(item) {
            return (item.shouldDelete === false)
        });
    }

    return state;
}

export default weather;