import React from 'react';
import logo from './img/logo.svg';

const EuroRate = (props) => {
    return(
        <div className="col-4 col-md-3 col-lg-2">
            <div className="row">
               <div className="flex-center-center-row">
                    <div>
                         <img src= {logo} alt="" className = "rate-logo"/>
                    </div>
                    <div>
                        {props.value}
                    </div>
               </div> 
            </div>
        </div>
    )
}

export default EuroRate;