import React, {Component} from 'react';
import {connect} from 'react-redux';

import {dispatchItem} from '../../actions/dispatchItem';

import DollarRateComponent from '../../components/Rate/DollarRate/DollarRate';

class DollarRate extends Component {

    componentDidMount() {
        this.props.onChangeValue(0);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <DollarRateComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[0]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DollarRate);