import React, {Component} from 'react';
import {connect} from 'react-redux';

import {BITCOIN_ID} from '../../actions/const';
import {dispatchItem} from '../../actions/dispatchItem';

import BitcoinComponent from '../../components/Crypto/Bitcoin/Bitcoin';

class Bitcoin extends Component {

    componentDidMount() {
        this.props.onChangeValue(BITCOIN_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <BitcoinComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        //потом оменять
        localState: state.item[BITCOIN_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Bitcoin);