import React, {Component} from 'react';
import {connect} from 'react-redux';

import {dispatchItem} from '../../actions/dispatchItem';
import {XRP_ID} from '../../actions/const';
import XRPComponent from '../../components/Crypto/XRP/XRP';

class XRP extends Component {

    componentDidMount() {
        this.props.onChangeValue(XRP_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <XRPComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.item[XRP_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(XRP);