
import React, {Component} from 'react';
import {connect} from 'react-redux';
import ShowWeatherComponent from '../../../components/Weather/ShowWeather/ShowWeather';


class ShowWeather extends Component {
    render() {
        return(
            <React.Fragment>
                <ul id = "showWeather__ul">
                {
                    //Перенести в component
                        (this.props.localState.length) ?
                        this.props.localState.map((element, index) => 
                            <li key = {index}
                                data-key = {element.id} 
                                style = {{textDecoration: element.shouldDelete ? 'line-through' : 'none'}}
                                >
                                {
                                    (element.temprature) ?
                                    (element.city + " " + element.temprature) :
                                    ("Sorry, cannot find weather for " + element.city)
                                }
                            </li>
                        ) :

                        <p className = "description">
                            Add your first city!<br/>
                            For example: "London", "Rome", "Moscow"
                        </p>
                    }
                </ul>
            </React.Fragment>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        localState: state.weather
    }
}

// const mapDispatchToProps = (dispatch) => {
//     return {

//     }
// }
export default connect(mapStateToProps)(ShowWeather);
