
//Rates
export const fetchDollarRate = () => {
    return(
    fetch('https://www.cbr-xml-daily.ru/daily_json.js')
        .then(response => response.json())
        .then(data => {
            return (data.Valute.USD.Value);
        }))
        .catch(err => console.error("Error", err))
}


