export const fetchBitcoin = () => {
    return(
        fetch('https://api.coinmarketcap.com/v1/ticker/bitcoin/')
            .then(response => response.json())
            .then(data => {
                console.log(data[0]);
                return (data[0].price_usd);
            }))
            .catch(err => console.error("Error", err)
    )
}

