
import topics from '../api/mockData/topics';
// const initialState = [{
//     id: 0,
//     name: "Курсы валют",
//     state: true
// }]

const initialState = topics;

const topic = (state = initialState, action) => {

    if(action.type  === "ADD_TOPIC") {
        return [
            ...state,
            {
                id: action.id,
                name: action.name,
                status: action.status
            }
        ]
    }

    if(action.type  === "TOGGLE_TOPIC") {
        console.log("TOGGLE_STATE", action.id);
        return state.map(element => (
            (element.id == action.id)
              ? {...element, status: !element.status }
              : element)
        );
    }
    
    if(action.type === "CHANGE_TOPIC") {
        return state.map(element => (
            (element.id == action.id) 
                ? {...element, value: action.value}
                : element)
        );
    }
    return state;
}

export default topic;