import React, {Component} from 'react';
import {connect} from 'react-redux';

import {dispatchItem} from '../../actions/dispatchItem';
import {ETHEREUM_ID} from '../../actions/const';
import EthereumComponent from '../../components/Crypto/Ethereum/Ethereum';

class Ethereum extends Component {

    componentDidMount() {
        this.props.onChangeValue(ETHEREUM_ID);
    }

    render() {
        return (
            (this.props.localState.status) ?
           (<React.Fragment>
               <EthereumComponent value = {this.props.localState.value}/>
            </React.Fragment>)
            :
            (<React.Fragment></React.Fragment>)
        )
    }
}

const mapStateToProps = (state) => {
    return {
        //потом поменять
        localState: state.item[ETHEREUM_ID]
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        onChangeValue: (id) => {
            dispatch(dispatchItem(id))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Ethereum);